<template>
	<header class="header">
		<div class="header-item header__pairs">
			<button v-on:click="dropdown = !dropdown" class="cur-pair">{{ pair.name }} <span><svg id="SVGDoc" width="14" height="7" xmlns="http://www.w3.org/2000/svg" version="1.1" xmlns:xlink="http://www.w3.org/1999/xlink" xmlns:avocode="https://avocode.com/" viewBox="0 0 14 7"><defs><path d="M283,20l-7,7l-7,-7v0" id="Path-0"/><clipPath id="ClipPath1016"><use xlink:href="#Path-0" fill="#ffffff"/></clipPath></defs><g transform="matrix(1,0,0,1,-269,-20)"><g><title>arrow</title><use xlink:href="#Path-0" fill-opacity="0" fill="#ffffff" stroke-linejoin="miter" stroke-linecap="butt" stroke-opacity="1" stroke="#aeaeae" stroke-miterlimit="50" stroke-width="2" clip-path="url(&quot;#ClipPath1016&quot;)"/></g></g></svg></span></button>

			<transition	name="slide">
				<ul v-if="dropdown" class="pairs">
					<li v-on:click="dropdown = !dropdown" v-for="item in pairs" class="pair__item"><router-link :to="{ name: 'pair', params: { id: item.path }}">{{ item.name }}</router-link></li>
				</ul>
			</transition>
		</div>
		<div class="header__logo"><img src="../assets/excaliburLogo.svg" alt="excalibur_alpha" class="header__logo-img"></div>
		<button @click="$parent.curTab = 'manage'" :class="{active: $parent.curTab == 'manage'}" class="header-item header__balances">
			<span class="header__balances__container">
				<span class="amount">{{ amount1 }}</span>
				<span class="symbol"> {{ pair.symbols[0] }}</span><br/>
				<span class="amount">{{ amount2 }}</span> 
				<span class="symbol"> {{ pair.symbols[1] }}</span>
			</span>
		</button>
		
	</header>
</template>

<script>
	import exchange from '../exchange.js'
	import settings from '../settings.json'

	import anime from 'animejs'

	import toolkit from './toolkit.vue'

	export default {
		name: 'headerMain',
		components: { toolkit, },
		data: function(){
			return {

				newAccount: false,
				incorrectKey: false,
				newAccountKey: '',
				dropdown: false,
				amount1: '...',
				amount2: '...',
				alert: true,
				menu: '',
				network: settings.network.name,

				metamaskToolkit: false,
				ladgerToolkit: false,

				copied: false,

				picked: localStorage.getItem('currentAddress'),

			}
		},
		computed: {
			web3() {
				return this.$parent.web3
			},
			metamaskAccount(){
				return this.$parent.metamaskAccount
			},
			localAccounts() {
				return this.$parent.accounts
			},
			walletButtons() {
				const vm = this;
				return vm.localAccounts.length !== 5
			},
			etherscan(){
				return settings.network.etherscan + 'address/' + settings.exchangeAddress
			},
			pairs() {
				return settings.pairs;
			},
			metamask(){
				return this.metamaskAccount == undefined || this.metamaskAccount == "" ? 'metamask-disconect' : 'metamask'
			},
			contract(){
				return this.$parent.contract
			}

		},
		props: {
			from: String,
			pair: Object,
		},
		methods: {
			closeInput(){
				const vm = this;
				vm.newAccount = false;
				vm.incorrectKey = false;
				vm.newAccountKey = '';
			},
			imortKey(){
				const vm = this;
				if (vm.newAccountKey.length > 64) {
					var account = vm.web3.eth.accounts.privateKeyToAccount(vm.newAccountKey);
					if (account.address !== vm.from) {
						vm.localAccounts.push(account);
						localStorage.setItem('accounts', JSON.stringify(vm.localAccounts))
					}
					vm.newAccountKey = '';
					vm.newAccount = false;
					vm.incorrectKey = false;
				}else{
					vm.incorrectKey = true;
				}
			},
			generateKey(){
				const vm = this;
				if (vm.localAccounts == null) {
					vm.localAccounts = [];
				}
				var account = vm.web3.eth.accounts.create();
				vm.picked = account.address;
				vm.localAccounts.push(account);
				localStorage.setItem('accounts', JSON.stringify(vm.localAccounts))
			},
			deleteWallet(address){
				const vm = this;
				if(address !== vm.from){
					vm.$parent.accounts = vm.localAccounts.filter(function(element) {
						return element.address !== address;
					});
					localStorage.setItem('accounts', JSON.stringify(vm.localAccounts))
				}
			},
			copy(id){
				const vm = this;
				let input = document.getElementById(id)
				input.select()
				try {
					var successful = document.execCommand('copy');
					var msg = successful ? 'successful' : 'unsuccessful';
					// alert('Address was copied ' + msg);
					vm.copied = true;

					setTimeout(function(){
						vm.copied = false;
					}, 3000)

				} catch (err) {
					console.log(err)
				}
				window.getSelection().removeAllRanges()
			},
			
			showMenu(){
				this.menu = this.menu == '' ? 'active' : ''
			},
			showBalance(){
				const vm = this;
				exchange.balanceOf(vm.contract, vm.pair.tokens[0], vm.from)
				.then(res => {
					vm.amount1 = +(res / 10**18).toFixed(10)
				})
				exchange.balanceOf(vm.contract, vm.pair.tokens[1], vm.from)
				.then(res => {
					vm.amount2 = +(res / 10**18).toFixed(10)
				})
			},
			
		},
		watch:{
			pair(){
				this.showBalance();
			},
			from(){
				this.showBalance();
			},
			picked(){
				var curAddress = this.picked;
				localStorage.setItem('currentAddress', curAddress);
				this.$parent.from = this.picked;
			}
		},

		created() {
			const vm = this;

			this.$parent.from = this.picked;

			// vm.contract.methods.balanceOf(vm.pair.tokens[0], vm.from).call(function(err, res){
			// 	if (!err){
			// 		vm.amount1 = +(res / 10**18).toFixed(10);
			// 		// alert(res);
			// 	} else {
			// 		// alert(err);
			// 	}
			// });


			setInterval(function () {
				try {
					vm.showBalance();
				} catch(e) {
					// statements
					console.log(e);
				}
				// vm.checkMetaMask();
			}, 5000)



		}
	}
</script>

